@import "./styles/utils.scss";

.SassComponent {
  display: flex;
  .box {
    // ì¼ë°˜ CSSì—ì„œëŠ” .SassComponent .box{ëž‘ ê°™ì€ê±°ìž„
    background: red;
    cursor: pointer;
    transition: all 0.3s ease-in;
    &.red {
      // .red í´ëž˜ìŠ¤ê°€ .boxì™€ ê°™ì´ í• ë•Œ
      background: $red;
      @include square(1);
    }
    &.orange {
      background: $orange;
      @include square(2);
    }
    &.yellow {
      background: $yellow;
      @include square(3);
    }
    &.green {
      background: $green;
      @include square(4);
    }
    &.blue {
      background: $blue;
      @include square(5);
    }
    &.indigo {
      background: $indigo;
      @include square(6);
    }
    &.violet {
      background: $violet;
      @include square(7);
    }
    &:hover {
      background: black;
    }
  }
}
